#contactPage {
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.contactDesc {
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm {
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}

.name, .email, .message{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border-color: transparent;
    border-radius: 0.5rem;
    background: rgb(40,40,40);
}

.name:hover, .email:hover, .message:hover{
    border-color:  rgb(0, 140, 255);
    transition: border 0.3s;
}

.name:focus, .email:focus, .message:focus{
    outline: none;
    border-color:  rgb(0, 140, 255);
}

.submitBtn {
    background: white;
    border-color: transparent;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}

.submitBtn:hover {
    border-color:  rgb(0, 140, 255);
    transition: border 0.3s;
}

.links {
    display: flex;
    flex-wrap: wrap;
}

.link {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}